<template>
  <div class="mp3play">
	  <!-- <h2>{{$store.state.mp3list}}</h2> -->
	  <!-- 2 *** -->
	  <!-- 使用局部组件，传递一个父组件中的事件 -->
	  <!-- 用@随便起一个别名 代表向组件中传递一个事件，事件名为aa -->
	  <!-- =后面写要传递的父组件中的事件名 代表将父组件中指定事件传递给子组件中-->
	<Login @aa='findAll'></Login>
	
		  
	<aplayer autoplay 
	  :music="{
	    title: mp3title,
	    artist: mp3artist,
	    src: mp3src,
	    pic: mp3pic
	  }" 
	  theme="#6952b6" 
	  
	/>
	
	<!-- <aplayer 
	 :music="music"
	  :list="songlist1" 
	  :float=true
	  theme="#6952b6"
	  repeat='list' 
	  :listFolded=true
	 
	/> -->
	
	<br>
	<div class="alert alert-warning" role="alert">
	    <div v-show="isshow">
	    	<small>1.点击左侧列表，选择某一期课程 </small><br>
	    	<small class="shuoming">2.点击右侧列表，选择某一课 </small><br>
	    	<small class="shuoming">3.点击上方播放控件，即可收听 </small>
	    </div>
	</div>
	
	
	<div class="container">
	  <div class="row">
	    <div class="col">
			<!-- 各期列表 -->
			<div id="user-jiazaiTS" style="color: #DC3545;"></div>
			<div id="user-shuchu">
				<!-- 查询列表 -->
				<div class="chaxun__list">
					<ul class="chaxun_content" v-for="item in kecheng">
						<li  @click="getmp3(item.key)">{{ item.name }}</li>
					</ul>
				</div>
			</div>
		</div>
	    <div class="col">
			<!-- 歌曲列表 -->
			<div class="music-player__list">
				<ul class="music__list_content" v-for="item in songlist">
					<li class="music__list__item " @click="playmp3(item)">{{ item.title }}</li>
				</ul>
			</div>
		</div>
	  </div>	
	</div>
	
	
	
  </div>
</template>

<script>
	
	var Bmob = require("@/zi____yuan/Bmob-2.2.5.min.js");
	Bmob.initialize("d120dedc62382103", "201068");
// @ is an alias to /src
import Login from '@/components/Login.vue'
import Aplayer from 'vue-aplayer'

export default {
  name: 'Mp3play',
  components: {
	Login,
	Aplayer
  },
  data() {
  			
		return {
			kecheng: [],
			mp3title: '天籁之音',
			mp3artist: '阿紫',
			mp3src:'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-aliyun-hjinhmzy11qaa4ebe2/a53b6460-134b-11eb-899d-733ae62bed2f.mp3',
			mp3pic:'https://ss2.meipian.me/users/9908934/b0ece710-41af-11eb-b978-7548ec61a560.png-mobile',
			songlist: [],
			songlist1: [],
			isshow: false,
		}
  },
  methods:{
	  // 1 ***
	  // 父组件中的事件
	  findAll(mp3s) {
		console.log('父组件中的事件')
		this.kecheng = mp3s
		if(this.kecheng !== []) {
			this.isshow = true
		}
		
	  },
	  
	  getmp3(mima) {
		 const query = Bmob.Query('mp3');
		 query.get(mima).then(res => {
		   // console.log(res)
		   console.log('成功')
		 
		   this.songlist = res.mp3list;
		   this.songlist1 = JSON.parse(JSON.stringify(res.mp3list).replace(/singer/g, 'artist').replace(/songUrl/g, 'src').replace(/imageUrl/g, 'pic'))
		   
		   
				  
		 }).catch(err => {
		   console.log(err)
		 })
	  },
	  
	  playmp3(item) {
		  this.mp3title = item.title
		  this.mp3artist = item.singer
		  this.mp3src = item.songUrl
		  this.mp3pic = item.imageUrl
	  },
	  
	  
  }
 
}
</script>

<style>
	#user-shuchu {
		background-color: #8A2BE2;
		color: #FFFFFF;
		
	}
	
	.music-player__list {
		background-color: #4B0082;
		color: #FFFFFF;
	}
	
	.chaxun_content>li, .music__list__item {
		cursor: pointer;
	}
	
	.chaxun_content>li:hover, .music__list__item:hover {
		background-color: red;
	}
	
	
				
</style>



