import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Book from '../views/Book.vue'
import Mp3play from '../views/Mp3play.vue'


Vue.use(VueRouter)

const routes = [
  {
	  path: '',
	  // path中什么都不写
	  // redirect重定向：重新定义一个方向
	  // 如此可以实现进入页面直接切换到首页
	  redirect: '/home',
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/book',
    name: 'Book',
    component: Book
  },
  {
    path: '/mp3play',
    name: 'Mp3play',
    component: Mp3play
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
